 
import { Component, OnInit, ViewChild } from '@angular/core';
import { ChartConfiguration, ChartData, ChartEvent, ChartType } from 'chart.js';
import { BaseChartDirective } from 'ng2-charts';
import { HelperService } from 'src/app/services/helper/helper.service';




import { AuthService } from 'src/app/services/auth/auth.service';
import { UntypedFormBuilder, UntypedFormGroup, Validators } from '@angular/forms';
import { ToastService } from 'src/app/services/toast/toast.service';
import { Router } from '@angular/router';

import { LoadingController } from '@ionic/angular';

 


  @Component({
    selector: 'app-ftm-network',
    templateUrl: './ftm-network.page.html',
    styleUrls: ['./ftm-network.page.scss'],
  })
  export class FtmNetworkPage implements OnInit {

    


  qrcode: any;
  name: any;
  content_loaded: boolean = false;
  address: any;
  balance: any; icon: any;
  wallets: any;
  usdt_transactions: any;
  constructor(
    private authService: AuthService,
    private loadingController: LoadingController,
    private formBuilder: UntypedFormBuilder,
    private toastService: ToastService,
    private router: Router,


    private helperService: HelperService
  ) { }


  ngOnInit() {

    this.loaddata().then(console.log);


  }

  async loaddata() {


    // Proceed with loading overlay
    const loading = await this.loadingController.create({
      cssClass: 'default-loading',
      message: 'Loading... Please be patient.',
      spinner: 'crescent'
    });


    await loading.present();


    this.authService
      .getETHWallet()
      .subscribe((data) => {
        this.wallets = data;


        this.address = data['address'];
        this.qrcode = data['qrcode'];
        this.icon = data['icon'];

        this.balance = data['balance'];
        this.name = data['name'];

        this.usdt_transactions = data['USDT']['transactions'];



        console.log(data);

        //  this.router.navigate(['/home']);
        this.content_loaded = true;
        loading.dismiss();

      });

  }

  copyText(text) {
    navigator.clipboard.writeText(text).then().catch(e => console.log(e));
  }


  sendto(send)
  {
 
    this.router.navigate([send]);
  }

}
